import WazeImage from "~@Assets/waze.svg";
import Ceremony1Image from "~@Assets/ceremony-1.jpg"
import Ceremony2Image from "~@Assets/ceremony-2.jpg"
import Ceremony3Image from "~@Assets/ceremony-3.jpg"

import { Download } from "lucide-react";
import { useNotifications } from "reapop";
import { useNavigate } from "react-router-dom";
import { useCallback, useEffect, useState, useTransition } from "react";
/**
 * @desc Global
 */
import { HTTP_STATUS, LINKS, TEXTS } from '~@Constants'
import { clsx } from '~@Services/clsx';
import * as Http from "~@Services/http";
import * as scroll from "~@Services/scroll";
import * as Scroll from '~@Components/Scroll';
import * as Image from "~@Components/Image";
import * as Section from '~@Components/Section';
import * as Button from "~@Components/Button";
import * as Overlay from '~@Components/Overlay';
import * as Iframe from "~@Components/Iframe";
import * as Typography from '~@Components/Typography';
/**
 * @desc Internal
 */
import * as GiftList from './Components/GiftList';

export function Ceremony() {
	const navigate = useNavigate();
	const notifications = useNotifications();
	const [gifts, setGifts] = useState([]);
	const [isPending, startTransition] = useTransition();

	const handleSelectGift = useCallback(function (event) {
		navigate(`/checkout/${event.target.dataset.id}`);
	}, [navigate]);

	const getGifts = useCallback(function () {
		startTransition(async function() {
			try {
				const response = await Http.execute('/api/v1/gift', 'GET', null);
				setGifts(response.data);
			} catch (error) {
				if (error.status === HTTP_STATUS.NOT_FOUND) {
					setGifts([]);
					return;
				}
				notifications.notify({
					status: 'error', title: TEXTS.ERROR, message: TEXTS.ERROR_DEFAULT
				});
			}
		});
	}, [notifications]);

	useEffect(function () {
		scroll.to('ceremony');
		const section = document.getElementById('ceremony__gifts');
		if (!section) return;
		const observer = new IntersectionObserver(async function (entries) {
			const [ entry ] = entries;
			if (entry.isIntersecting) getGifts();
		}, { threshold: 0.2 });
		observer.observe(section);
		return function () {
			observer.unobserve(section);
		}
	}, [getGifts]);

	return (
		<Section.Group>
			<Section.UniqueTitle text='Ceremony'/>
			<Section.Divider id='ceremony' className={clsx([
				'items-center', 'h-svh w-full', 'bg-dark-900'
			])}>
				<Image.Background image={Ceremony1Image || ''} alt="Banner" />
				<Typography.Group className={clsx(['absolute', 'z-10 pl-8 pr-8'])}>
					<Typography.Title text='Cerimonia'/>
					<Typography.Paragraph>
						 com muita alegria que convidamos voc para a nossa Cerimnia de Casamento! Este ser um
						momento inesquecvel, em que celebraremos juntos o comeo de uma nova jornada em nossas vidas.
					</Typography.Paragraph>
				</Typography.Group>
				<Overlay.Full/>
				<Scroll.Indicator/>
			</Section.Divider>
			<Section.Divider id='ceremony__images' className={clsx([
				'flex-col  items-center', 'p-8 min-h-[50vh] w-full', 'bg-dark-800'
			])}>
				<div className='relative flex overflow-x-auto w-auto h-[400px] lg:gap-2'>
					<img src={Ceremony1Image || ''} alt='' className={clsx([
						'w-[300px]', 'object-cover aspect-square',
					])}/>
					<img src={Ceremony2Image || ''} alt='' className={clsx([
						'w-[400px]', 'object-cover aspect-square',
					])}/>
					<img src={Ceremony3Image || ''} alt='' className={clsx([
						'w-[300px]', 'object-cover aspect-square',
					])}/>
				</div>
			</Section.Divider>
			<Section.Divider id='ceremony__locale' className={clsx([
				'flex-col items-center', 'p-8 py-20 min-h-screen w-full', 'bg-dark-900'
			])}>
				<Typography.Group>
					<Typography.Title text='Local'/>
					<Typography.Paragraph>
						No dia <span className='font-bold text-purple-300'>12 de julho de 2025</span>, teremos a honra
						de receb-los s <span className='font-bold text-purple-300'>18 horas da tarde</span> onde ser
						realizada na Churrascaria Kiesa.
					</Typography.Paragraph>
				</Typography.Group>
				<Iframe.Root src={LINKS.EMBED_CEREMONY}/>
				<Button.Link
					href={LINKS.WAZE_CEREMONY}
					color='text-waze-100 border-waze-100'
					text='Abrir no Waze'
					Icon={<img src={WazeImage || ''} alt='Open Waze' width={25}/>}
				/>
			</Section.Divider>
			<Section.Divider id='ceremony__important' className={clsx([
				'flex-col items-center', 'p-8 py-20 min-h-screen w-full', 'bg-dark-800'
			])}>
				<Typography.Group>
					<Typography.Title text='Importante'/>
					<Typography.Paragraph>
						Aqui em breve teremos informaes importantes para nosso grande dia.
					</Typography.Paragraph>
				</Typography.Group>
			</Section.Divider>
			<Section.Divider id='ceremony__gifts' className={clsx([
				'flex-col items-center', 'p-8 py-20 min-h-screen w-full', 'bg-dark-900'
			])}>
				<Typography.Group>
					<Typography.Title text='Presentes'/>
					<Typography.Paragraph>
						Ol! Nos ajude a realizar nosso sonho: escolha um presente, pague via Pix, carto ou compre na
						sua loja favorita.  s selecionar e seguir as instrues. Obrigado por fazer parte desse
						momento! <span className='font-bold text-purple-300'>Importante: o presente  opcional!</span>
					</Typography.Paragraph>
				</Typography.Group>
				<GiftList.Root>
					{isPending ? (
						<></>
					) : gifts.map(function (gift) {
						return (
							<GiftList.Item key={gift.id} gift={gift} onClick={handleSelectGift} />
						)
					})}
				</GiftList.Root>
			</Section.Divider>
			<Section.Divider id='event__gifts' className={clsx([
				'flex-col items-center', 'p-8 py-20 min-h-[50vh] w-full', 'bg-dark-800'
			])}>
				<Typography.Group>
					<Typography.Title text='Downloads'/>
					<Typography.Paragraph>
						Abaixo est o nosso cdigo de conduta. Caso voc seja padrinho ou madrinha, encontrar tambm o seu guia especfico.
					</Typography.Paragraph>
				</Typography.Group>
				<Button.Link
					href='/files/godparents_guide.pdf'
					color='text-purple-300 border-purple-300'
					text='Guia dos padrinhos'
					Icon={<Download className='text-purple-300' width={25}/>}
				/>
			</Section.Divider>
		</Section.Group>
	)
}
