import { memo } from "react";
import PropTypes from "prop-types";
/**
 * @desc Internal
 */
import { clsx } from "~@Services/clsx.js";

function BackgroundComponent({ image, alt }) {
	return (
		<img loading='lazy' className='h-full w-full object-cover object-center' src={image} alt={alt}/>
	);
}

BackgroundComponent.propTypes = {
	alt: PropTypes.string,
	image: PropTypes.string,
}

export function GrayScaleComponent({ image, alt, children }) {
	return (
		<figure className='group relative flex'>
			<img
				alt={alt}
				src={image}
				loading='lazy'
				className={clsx([
					'h-auto max-h-[500px]',
					'rounded-xl object-cover aspect-square',
					'transition-all duration-300 ease-in-out group-hover:grayscale'
				])}
			/>
			<figcaption className={clsx([
				'opacity-0 hover:opacity-100',
				'transition-opacity duration-500',
				'absolute', 'rounded-xl', 'h-full w-full p-8',
				'flex flex-col justify-end',
				'text-white text-justify text-sm',
				'bg-gradient-to-t from-black/90 via-black/80 to-transparent',
			])}>
				{children}
			</figcaption>
		</figure>
	)
}

GrayScaleComponent.propTypes = {
	alt: PropTypes.string,
	image: PropTypes.string,
	children: PropTypes.node,
}

export const Background = memo(BackgroundComponent);
export const GrayScale = memo(GrayScaleComponent);
