import { memo } from "react";
import PropTypes from "prop-types";
import { useNotifications } from "reapop";
import { ChevronRight } from "lucide-react";
/**
 * @desc Global
 */
import { TEXTS } from "~@Constants";
import { clsx } from "~@Services/clsx.js";

function GiftCardComponent({ gift }) {
	const notifications = useNotifications();

	function handleClick(event) {
		event.preventDefault();
		try {
			const url = gift.links.website;
			new URL(url);
			window.location.href = url;
		} catch {
			notifications.notify({
				status: 'error', title: TEXTS.WARNING, message: 'Ainda no escolhemos esse item, se tiver uma sugesto nos envie.'
			});
		}
	}

	return (
		<a
			onClick={handleClick}
			className={clsx([
				'flex justify-between items-center',
				'bg-white text-black rounded-xl p-4 w-full',
				'hover:bg-purple-300 hover:text-white transition-all duration-200',
				'cursor-pointer'
			])}
		>
			<div>
				<p className='text-xl'>
					{gift.name} ({gift.description})
				</p>
				<p className='text-sm'>
					
				</p>
				<p className='text-2xl'>
					{gift.price.toLocaleString('pt-br', {
						style: 'currency',
						currency: 'BRL'
					})}
				</p>
			</div>
			<ChevronRight size={26}/>
		</a>
	);
}

GiftCardComponent.propTypes = {
	gift: PropTypes.object,
}

export const GiftCard = memo(GiftCardComponent);
