/**
 * Combines class names.
 *
 * @param {string[]} classes - An array of class names to include unconditionally.
 * @param {Object<string, boolean>} [conditions] - An object where the keys are class names
 * and the values are booleans. If the value is `true`, the corresponding class name
 * is included in the result.
 * @returns {string} A single string of space-separated class names.
 */
export function clsx(classes, conditions) {
    let className = '';
    for (let c of classes) {
        className += ' ' + c;
    }
    for (let c in conditions) {
        if (conditions[c]) {
            className += ' ' + c;
        }
    }
    return className;
}
