import { DateTime } from 'luxon';
import { memo } from "react";
import PropTypes from "prop-types";
import { Check, X, Minus } from "lucide-react";
/**
 * @desc Global
 */
import { clsx } from "~@Services/clsx.js";

// eslint-disable-next-line react-refresh/only-export-components
export const ICON_MAP = {
	confirmed: <Check className="text-green-400" size={26} />,
	unconfirmed: <Minus className="text-black" size={26} />,
	waiting: <X className="text-red-400" size={26} />,
};

function RootComponent({ children }) {
	return (
		<ul className='w-full md:w-full lg:w-2/3 xl:w-1/2'>
			{children}
		</ul>
	);
}

RootComponent.propTypes = {
	children: PropTypes.node.isRequired,
}

/**
 * @typedef {Object} Guest
 * @property {number} id - The ID of the guest.
 * @property {string} name - The name of the guest.
 * @property {string} code - The code of the guest.
 * @property {boolean} confirmed - The confirmation of the guest.
 * @property {string} created_at - The timestamp when the guest was added.
 */

/**
 * @param {Guest} guest
 */
function ItemComponent({ guest }) {
	return (
		<li
			className={clsx([
				'group',
				'flex justify-between items-center',
				'bg-white rounded-xl mb-2 px-4 py-4 w-full',
				'hover:bg-purple-300 hover:text-white transition-all duration-200',
			])}>
			<div>
				<p className={clsx([
					'text-lg'
				], {
					'line-through': !guest.confirmed && guest.created_at
				})}>{guest.id} - {guest.name} ({guest.code})</p>
				<p className='text-sm'>
					{guest.confirmed ? 'Presena comfirmada' : guest.created_at ? 'No ir comparecer' : 'Aguardando confirmao'}
				</p>
				<p className='text-xs text-gray-600 group-hover:text-white'>
					Confirmado em: {guest.created_at && DateTime.fromSQL(guest.created_at).toLocaleString(DateTime.DATE_SHORT)}
				</p>
			</div>
			{ICON_MAP[guest.confirmed ? 'confirmed' : guest.created_at ? 'unconfirmed' : 'waiting']}
		</li>
	);
}

ItemComponent.propTypes = {
	guest: PropTypes.object.isRequired,
}

export const Root = memo(RootComponent);
export const Item = memo(ItemComponent);
