import PropTypes from "prop-types";
import { memo } from 'react';
import { motion } from 'framer-motion';
import { useLocation } from 'react-router-dom';

import { clsx } from '~@Services/clsx';

function RootComponent({ className, isBlur, children, ...props }) {
	return (
		<motion.ul
			{...props}
			className={clsx([
				'flex', 'backdrop-blur-xl rounded-xl z-20', className
			], {
				'bg-black/20': !isBlur, 'bg-white/20': isBlur
			})}
		>
			{children}
		</motion.ul>
	);
}

RootComponent.propTypes = {
	className: PropTypes.string,
	isBlur: PropTypes.bool,
	children: PropTypes.node.isRequired,
}

function ItemComponent({ className, path, children, ...props }) {
	const location = useLocation();
	return (
		<motion.li
			{...props}
			className={clsx([
				'font-semibold text-white text-xl rounded-xl',
				'hover:bg-white/30', className
			], {
				'bg-white/30': location.pathname === path,
			})}
		>
			{children}
		</motion.li>
	)
}

ItemComponent.propTypes = {
	className: PropTypes.string,
	path: PropTypes.string,
	children: PropTypes.node,
}

function ButtonComponent({ className, isBlur, children, ...props }) {
	return (
		<motion.button
			{...props}
			className={clsx([
				'flex justify-center items-center',
				'font-semibold text-white text-xl rounded-xl backdrop-blur-xl',
				className
			], {
				'bg-black/20': !isBlur, 'bg-white/20': isBlur
			})}
		>
			{children}
		</motion.button>
	)
}

ButtonComponent.propTypes = {
	className: PropTypes.string,
	isBlur: PropTypes.bool,
	children: PropTypes.node
}

export const Root = memo(RootComponent);
export const Item = memo(ItemComponent);
export const Button = memo(ButtonComponent);

