import PropTypes from "prop-types";
import { motion } from 'framer-motion';
import { Link } from 'react-router-dom';
/**
 * @desc
 */
import { clsx } from '~@Services/clsx';
/**
 * @desc
 */
import * as Menu from './Menu';
import { menuVariants, itemVariants } from './motion';

const pages = [
	{ key: 'Home', value: '/' },
	{ key: 'Ch Bar', value: '/event' },
	{ key: 'Cerimnia', value: '/ceremony' },
	// { key: 'Presentes', value: '/gifts' },
	{ key: 'Confirmao', value: '/confirmation' }
];

export function Default({ isBlur }) {
	return (
		<motion.nav className={clsx([ 'hidden w-auto', 'md:block' ])}>
			<Menu.Root
				isBlur={isBlur}
				className='h-full'
			>
				{pages.map(function({ key, value }) {
					return (
						<Menu.Item
							key={key}
							path={value}
						>
							<Link
								to={value}
								className={clsx([
									'flex justify-center items-center', 'py-3 w-36 h-full'
								])}
							>
								{key}
							</Link>
						</Menu.Item>
					);
				})}
			</Menu.Root>
		</motion.nav>
	);
}

Default.propTypes = {
	isBlur: PropTypes.bool,
}

export function Mobile({ isBlur, isOpen, handleMenu }) {
	return(
		<motion.nav
			initial={false}
			animate={isOpen ? "open" : "close"}
			className={clsx([ 'block ml-8 mr-8','md:hidden' ])}
		>
			<Menu.Button
				isBlur={isBlur}
				onClick={handleMenu}
				whileTap={{ scale: .9 }}
				className='p-3 mb-3 w-full'
			>
				Menu
			</Menu.Button>
			<Menu.Root
				isBlur={isBlur}
				variants={menuVariants}
				style={{ pointerEvents: isOpen ? "auto" : "none" }}
				className='flex-col'
			>
				{pages.map(function ({ key, value }) {
					return (
						<Menu.Item
							key={key}
							path={value}
							onClick={handleMenu}
							variants={itemVariants}
						>
							<Link
								to={value}
								className={clsx([
									'flex justify-center items-center', 'p-3 w-80'
								])}
							>
								{key}
							</Link>
						</Menu.Item>
					);
				})}
			</Menu.Root>
		</motion.nav>
	);
}

Mobile.propTypes = {
	isBlur: PropTypes.bool,
	isOpen: PropTypes.bool,
	handleMenu: PropTypes.func
}
