import PropTypes from "prop-types";
import {ChevronRight} from "lucide-react";

import {clsx} from "~@Services/clsx.js";

/**
 * @typedef {Object} Event
 * @property {number} id - The ID of the event.
 * @property {string} name - The name of the event.
 * @property {string} date - The code of the event.
 */

/**
 *
 * @param {Event} event
 * @param onClick
 * @returns {JSX.Element}
 * @constructor
 */
export function Event({ event, onClick }) {
	return (
		<li
			data-id={event.id}
			onClick={onClick}
			className={clsx([
				'flex justify-between items-center',
				'bg-white rounded-xl mb-2 px-4 py-4 w-full',
				'hover:bg-purple-300 hover:text-white transition-all duration-200',
				'cursor-pointer'
			])}>
			<div className='pointer-events-none'>
				<p className='text-lg'>{event.name}</p>
				<p className='text-sm'>{event.date}</p>
			</div>
			<ChevronRight className='pointer-events-none' size={22}/>
		</li>
	)
}

Event.propTypes = {
	event: PropTypes.object.isRequired,
	onClick: PropTypes.func,
}


/**
 * @typedef {Object} Guest
 * @property {number} id - The ID of the guest.
 * @property {string} name - The name of the guest.
 * @property {string} side - The side of the guest.
 * @property {boolean} checked - The checked of the guest.
 * @property {string} relationship - The relationship of the guest.
 */

/**
 *
 * @param guest
 * @param onChange
 * @returns {JSX.Element}
 * @constructor
 */
export function Guest({ guest, onChange }) {
	return (
		<li
			className={clsx([
				'bg-white mb-2 px-4 py-2 w-full flex justify-between items-center rounded-xl'
			], {
				'bg-purple-300': guest.checked
			})}
		>
			<div>
				<p className='text-lg'>{guest.name}</p>
				<p className='text-sm text-gray-500'>
					{guest.relationship} do(a) {guest.side}
				</p>
			</div>
			<input
				type="checkbox"
				name={guest.name}
				checked={guest.checked}
				className={clsx([
					'size-5 rounded',
				])}
				onChange={onChange}
			/>
		</li>
	)
}

Guest.propTypes = {
	guest: PropTypes.object.isRequired,
	onChange: PropTypes.func,
}
