import { memo } from "react";
import PropTypes from "prop-types";
/**
 * @desc Internal
 */
import { clsx } from "~@Services/clsx.js";

function DefaultComponent() {
	return (
		<></>
	);
}

function LinkComponent({ href, text, color, Icon }) {
	return (
		<a
			href={href}
			target='_blank'
			className={clsx([
				'flex items-center justify-center', 'p-3 mb-4',
				'font-semibold text-lg rounded-xl border-[1px]',
				'w-full md:w-full lg:w-2/3 xl:w-1/2',
				color
			])}
		>
			{Icon}
			<p className='ml-2'>{text}</p>
		</a>
	)
}

LinkComponent.propTypes = {
	href: PropTypes.string,
	text: PropTypes.string,
	color: PropTypes.string,
	Icon: PropTypes.func,
}

export const Link = memo(LinkComponent);
export const Default = memo(DefaultComponent);
