/**
 * Executes an HTTP request.
 *
 * @param {string} uri - The endpoint URI.
 * @param {string} method - The HTTP method (e.g., 'GET', 'POST').
 * @param {Object} [body] - The request payload, if applicable.
 * @returns {Promise<{status: number, data: any}>} The response status and data.
 * @throws {Object} Throws an error with status and data if the request fails.
 */
export async function execute(uri, method, body) {
	const url = `${import.meta.env.VITE_API_URL}${uri}`;
	const headers = {
		Accept: "application/json",
		"Content-Type": "application/json;charset=UTF-8",
	};
	const options = {
		method,
		headers,
		...(body && { body: JSON.stringify(body) }),
	};
	let data = {};
	const response = await fetch(url, options);
	if (!response.ok) {
		throw { status: response.status, data };
	}
	const contentType = response.headers.get('content-type');
	if (!contentType) {
		return { status: response.status, data };
	}
	if (contentType.includes('application/json')) {
		data = await response.json();
	}
	return { status: response.status, data };
}
