import { memo } from "react";
import PropTypes from "prop-types";
/**
 * @desc Global
 */
import { clsx } from "~@Services/clsx.js";

function RootComponent({ children }) {
	return (
		<ul className={clsx([
			'flex overflow-x-auto',
			'w-full md:w-full lg:w-2/3 xl:w-1/2'
		])}>
			{children}
		</ul>
	)
}

RootComponent.propTypes = {
	children: PropTypes.node.isRequired,
}

/**
 * @typedef {Object} Gift
 * @property {number} id - The ID of the gift.
 * @property {string} name - The name of the gift.
 * @property {number} price - The price of the gift.
 * @property {boolean} reserved - THe is reserved gift
 * @property {boolean} available - The is available gift
 */
/**
 *
 * @param {Gift} gift
 * @param onClick
 * @returns {JSX.Element}
 * @constructor
 */
function ItemComponent({ gift, onClick }) {
	return (
		<li
			className={clsx([
				'flex flex-col justify-end', 'bg-dark-800',
				'px-8 py-4 rounded-xl h-[300px]'
			], {
				'ml-6': gift.id !== 1
			})}
		>
			<hgroup className='text-white text-center w-[186px]'>
				<p className='text-xl mb-2'>{gift.name}</p>
				<h2 className='text-2xl mb-6'>
					{gift.price.toLocaleString('pt-br', {
						style: 'currency',
						currency: 'BRL'
					})}
				</h2>
			</hgroup>
			<button
				data-id={gift.id}
				onClick={onClick}
				disabled={!gift.available}
				className={clsx([
					'flex items-center justify-center', 'p-3 mb-4',
					'font-semibold text-lg rounded-xl border-[1px]',
				], {
					'text-gray-300 border-gray-300 cursor-not-allowed': gift.reserved && !gift.available,
					'text-green-300 border-green-300 cursor-pointer': !gift.reserved && gift.available,
					'text-red-300 border-red-300 cursor-not-allowed': !gift.reserved && !gift.available,
				})}
			>
				{gift.reserved ? 'Reservado' : gift.available ? 'Presentear' : 'Indisponivel'}
			</button>
		</li>
	);
}

ItemComponent.propTypes = {
	gift: PropTypes.object.isRequired,
	onClick: PropTypes.func,
}

export const Root = memo(RootComponent);
export const Item = memo(ItemComponent);

