import { DateTime } from 'luxon';
import { useEffect, useState } from 'react';
/**
 * @desc Global
 */
import { clsx } from '~@Services/clsx';

const counter = [
	{ key: 'days', value: 'Dias' },
	{ key: 'hours', value: 'Horas' },
	{ key: 'minutes', value: 'Minutos' },
	{ key: 'seconds', value: 'Segundos' }
];

export function CountDown() {
	const [ count, setCount ] = useState({ days: 0, hours: 0, minutes: 0, seconds: 0 });

	useEffect(function() {
		const nowDate = DateTime.now();
		const targetDate = DateTime.fromObject({ 
			year: 2025, month: 7, day: 12, hour: 18, minute: 0 
		});
		const interval = setInterval(function() {
			const diffDate = targetDate.diff(nowDate, ['days', 'hours', 'minutes', 'seconds']);
			const { days, hours, minutes, seconds } = diffDate.toObject();
			setCount({ days, hours, minutes, seconds: Math.floor(seconds) });
		}, 1000);
		return function() {
			clearInterval(interval);
		}
	}, [ count ]);
	
	return (
		<div className={clsx([
			'flex flex-col justify-center items-center',
			'w-full h-[50vh] p-8 py-20', 'bg-dark-800'
		])}>
			<ul className='flex flex-wrap gap-6 md:gap-8 lg:gap-10'>
				{counter.map(function ({ key, value }) {
					return (
						<li key={key} className='flex-1 text-center'>
							<h2 className={clsx([
								'text-5xl text-white mb-2',
								'md:text-6xl lg:text-7xl'
							])}>
								{count[key]}
							</h2>
							<p className='text-sm md:text-lg text-gray-400'>{value}</p>
						</li>
					);
				})}
			</ul>
		</div>
	);
}
