import { useNotifications } from "reapop";
import { useEffect, useCallback, useReducer, useTransition } from "react";
/**
 * @desc Global
 */
import { TEXTS, HTTP_STATUS } from "~@Constants";
import { clsx } from "~@Services/clsx";
import * as Http from "~@Services/http";
import * as Select from "~@Components/Select";
import * as Section from "~@Components/Section";
import * as Typography from "~@Components/Typography";
/**
 * @desc Internal
 */
import { reducer, TYPES, INITIAL_STATE } from "./reducer.js";
import * as GuestList from "./Components/GuestList";
import * as CounterList from "./Components/CounterList";

export function Admin() {
	const notifications = useNotifications();
	const [state, dispatch] = useReducer(reducer, INITIAL_STATE);
	const [isPending, startTransition] = useTransition();

	const handleChange = useCallback(async function (event) {
		dispatch({ type: TYPES.SET_EVENT, payload: { event: event.target.value } });
	}, []);

	const getGuests = useCallback(function () {
		startTransition(async function() {
			try {
				const response = await Http.execute(`/api/v1/guest/admin?event=${state.event}`, 'GET', null);
				const status = { ...INITIAL_STATE.status };
				const guests = response.data.map(function (guest) {
					guest.confirmed ? status.confirmed++ : guest.created_at ? status.unconfirmed++ : status.waiting++;
					return guest;
				});
				dispatch({ type: TYPES.SET_GUEST, payload: { status, guests } });
			} catch (error) {
				if (error.status === HTTP_STATUS.NOT_FOUND) {
					dispatch({ type: TYPES.RESET });
					return;
				}
				notifications.notify({
					status: 'error', title: TEXTS.ERROR, message: TEXTS.ERROR_DEFAULT
				});
			}
		});
	}, [notifications, state.event]);

	useEffect(() => {
		if (state.event) getGuests();
	}, [state.event, getGuests]);

	return (
		<Section.Group>
			<Section.UniqueTitle text='Admin'/>
			<Section.Divider id='admin' className={clsx([
				'flex-col items-center', 'p-8 py-20 min-h-svh w-full', 'bg-dark-800'
			])}>
				<Typography.Group className='mt-12'>
					<Typography.Title text='Admin'/>
					<Typography.Paragraph>
						Esta pgina  destinada ao gerenciamento de dados e configuraes do sistema.
					</Typography.Paragraph>
				</Typography.Group>
				<Select.Default
					id="events"
					value={state.event}
					onChange={handleChange}
					options={[
						{ value: '1', label: 'Ch Bar' },
						{ value: '2', label: 'Casamento' }
					]}
				/>
				{isPending ? (
					<></>
				) : (
					<CounterList.Root>
						{Object.entries(state.status).map(function ([key, value]) {
							return (
								<CounterList.Item key={key} type={key} value={value}/>
							)
						})}
					</CounterList.Root>
				)}
				{isPending ? (
					<></>
				) : (
					<GuestList.Root>
						{state.guests.map(function (guest) {
							return (
								<GuestList.Item key={guest.id} guest={guest} />
							)
						})}
					</GuestList.Root>
				)}
			</Section.Divider>
		</Section.Group>
	)
}
