import { memo } from "react";
import PropTypes from "prop-types";
/**
 * @desc Internal
 */
import { clsx } from "~@Services/clsx.js";

function RootComponent({ src, height = 'h-[322px]' }) {
	return (
		<iframe
			src={src}
			allowFullScreen=""
			loading="lazy"
			className={clsx([
				'border-0 rounded-xl', 'mb-6', height,
				'w-full md:w-full lg:w-2/3 xl:w-1/2'
			])}
		/>
	)
}

RootComponent.propTypes = {
	src: PropTypes.string,
	height: PropTypes.string,
}

export const Root = memo(RootComponent);
