import { memo } from "react";
import PropTypes from "prop-types";
/**
 * @desc Internal
 */
import { clsx } from "~@Services/clsx.js";

function RootComponent({ className, children }) {
	return (
		<hgroup className={clsx([
			'flex flex-col items-center', 'mb-6',
			'text-white', 'sm:w-full md:w-full lg:w-2/3 xl:w-1/2',
			className
		])}>
			{children}
		</hgroup>
	);
}

RootComponent.propTypes = {
	className: PropTypes.string,
	children: PropTypes.node.isRequired,
}

function TitleComponent({ className, text }) {
	return (
		<h2 className={clsx([
			'font-halimun text-center', 'mb-10',
			'text-3xl md:text-4xl lg:text-4xl xl:text-4xl',
			className
		])}>
			{text}
		</h2>
	);
}

TitleComponent.propTypes = {
	className: PropTypes.string,
	text: PropTypes.string.isRequired,
}

function ParagraphComponent({ children, className }) {
	return (
		<p className={clsx([ 'text-justify', 'text-lg', className ])}>
			{children}
		</p>
	);
}

ParagraphComponent.propTypes = {
	children: PropTypes.node.isRequired,
	className: PropTypes.string,
}

export const Group = memo(RootComponent);
export const Title = memo(TitleComponent);
export const Paragraph = memo(ParagraphComponent);
