import { memo } from "react";
import PropTypes from "prop-types";
import { clsx } from "~@Services/clsx.js";

import { ICON_MAP } from "./GuestList.jsx";

function RootComponent({ children }) {
	return (
		<ul className={clsx([
			'flex', 'gap-2', 'w-full md:w-full lg:w-2/3 xl:w-1/2', 'mb-6'
		])}>
			{children}
		</ul>
	);
}

RootComponent.propTypes = {
	children: PropTypes.node.isRequired,
}

function ItemComponent({ type, value }) {
	return (
		<li
			className={clsx([
				'px-8 py-4 flex-1 flex flex-col items-center', 'bg-white', 'rounded-xl'
			])}>
			{ICON_MAP[type]}
			<p className='text-xl'>{value}</p>
		</li>
	)
}

ItemComponent.propTypes = {
	type: PropTypes.string.isRequired,
	value: PropTypes.number.isRequired,
}

export const Root = memo(RootComponent);
export const Item = memo(ItemComponent);
