import BannerImage from '~@Assets/confirmation-1.jpg';

import {useNotifications} from 'reapop'
import {useLocation, useNavigate} from 'react-router-dom';
import {useContext, useEffect, useRef, useState} from 'react';
/**
 * @desc Global
 */
import {EVENTS, HTTP_STATUS, TEXTS} from '~@Constants';
import {clsx} from '~@Services/clsx';
import * as Http from '~@Services/http';
import * as scroll from '~@Services/scroll';
import {Render} from '~@Components/Render';
import * as Scroll from '~@Components/Scroll';
import * as Image from "~@Components/Image";
import * as Section from '~@Components/Section';
import * as Overlay from '~@Components/Overlay';
import * as Typography from '~@Components/Typography';
import { HealthCheckContext } from "~@Context/HealthCheckContext";
/**
 * @desc Internal
 */
import * as Item from './Components/Item';

export function Confirmation() {
	const health = useContext(HealthCheckContext);
	const location = useLocation();
	const navigate = useNavigate();
	const notifications = useNotifications();
	const buttonRef = useRef(null);
	const inputRef = useRef(null);

	const [code, setCode] = useState('');
	const [event, setEvent] = useState(null);
	const [events, setEvents] = useState([]);
	const [guests, setGuests] = useState([]);
	const [isLoading, setIsLoading] = useState(false);

	async function handleSearchGuests(event) {
		event.preventDefault();
		setIsLoading(true);
		try {
			const uri = new URLSearchParams({code: inputRef.current.value});
			const response = await Http.execute(`/api/v1/guest?${uri.toString()}`, 'GET', null);
			const eventsMap = {
				[EVENTS.EVENT]: { id: EVENTS.EVENT, name: 'Cha Bar', date: '08/03/2025' },
				[EVENTS.CEREMONY]: { id: EVENTS.CEREMONY, name: 'Casamento', date: '12/07/2025' }
			}
			const events = response.data.events.map(function (event) {
				return eventsMap[event.id];
			});
			setEvents(events);
			const guests = response.data.guests.map(function (item) {
				return {...item, checked: false};
			});
			setGuests(guests);
			setCode(inputRef.current.value);
		} catch (error) {
			notifications.dismissNotifications();
			if (error.status === HTTP_STATUS.NOT_FOUND) {
				return notifications.notify('', {
					status: 'warning', title: TEXTS.WARNING,
					message: 'No encontramos o cdigo no sistema. Certifique-se de que ele foi digitado corretamente e tente novamente.'
				});
			}
			notifications.notify({
				status: 'error', title: TEXTS.ERROR, message: TEXTS.ERROR_DEFAULT
			});
		} finally {
			setIsLoading(false);
		}
	}

	function handleMarkGuestChecked(event) {
		const {name, checked} = event.target;
		const marked = guests.map(function (guest) {
			if (guest.name === name) {
				guest.checked = checked;
			}
			return guest;
		});
		setGuests(marked);
	}

	function handleChooseEvent(event) {
		setEvent(Number(event.target.dataset.id));
	}

	async function handleConfirmationEvent() {
		try {
			const uri = new URLSearchParams({code});
			await Http.execute(`/api/v1/guest/confirm?${uri.toString()}`, 'POST', {event, guests});
			notifications.dismissNotifications();
			notifications.notify({
				status: 'success', title: TEXTS.CONGRATULATION,
				message: 'Confirmao realizada com sucesso.',
			});
			navigate('/confirmed');
		} catch (error) {
			notifications.dismissNotifications();
			if (error.status === HTTP_STATUS.CONFLICT) {
				return notifications.notify('', {
					status: 'warning', title: TEXTS.WARNING,
					message: 'Voc j confirmou sua presena neste evento. Caso precise de ajuda, entre em contato conosco!'
				});
			}
			notifications.notify({
				status: 'error', title: TEXTS.ERROR, message: TEXTS.ERROR_DEFAULT,
			});
		}
	}

	useEffect(function () {
		const params = new URLSearchParams(location.search);
		if (!params.has('code')) {
			scroll.to('confirmation');
			return;
		}
		scroll.to('confirmation__code');
		inputRef.current.value = params.get('code');
		inputRef.current.focus();
		buttonRef.current.click();
	}, [location]);

	return (
		<Section.Group>
			<Section.UniqueTitle text='Confirmation'/>
			<Section.Divider id='confirmation' className={clsx([
				'flex-col items-center', 'h-screen w-full'
			])}>
				<Image.Background image={BannerImage || ''} alt='Banner'/>
				<Typography.Group className='absolute z-10 pl-8 pr-8'>
					<Typography.Title text='Confirmacao'/>
					<Typography.Paragraph>
						Queridos amigos e familiares, estamos muito felizes em compartilhar esse momento to especial
						com vocs. Para que possamos organizar tudo da melhor forma possvel e garantir que cada detalhe
						esteja perfeito, pedimos que confirmem sua presena at o dia <span
						className='font-bold text-purple-300'>21/02/2025</span>. Sua presena  muito importante para
						ns, e estamos ansiosos para celebrar esse dia inesquecvel ao lado de pessoas to queridas.
						Contamos com vocs para tornar nosso casamento ainda mais especial!
					</Typography.Paragraph>
				</Typography.Group>
				<Overlay.Full/>
				<Scroll.Indicator/>
			</Section.Divider>
			<Section.Divider id='confirmation__code' className={clsx([
				'flex-col items-center', 'p-8 h-screen w-full', 'bg-dark-800'
			])}>
				<Render condition={Boolean(guests.length)}>
					<div className='w-full md:w-full lg:w-2/3 xl:w-1/2'>
						<Render condition={!event}>
							<hgroup className={clsx([
								'flex flex-col items-center', 'mb-6', 'text-white',
							])}>
								<Typography.Title text='Eventos'/>
								<Render condition={Boolean(events.length)}>
									<Typography.Paragraph>
										Por favor, escolha o evento que voc deseja confirmar. Para isso, basta selecionar
										uma das opes abaixo e seguir as instrues para concluir a confirmao com
										facilidade.
									</Typography.Paragraph>
								</Render>
								<Render condition={Boolean(!events.length)}>
									<Typography.Paragraph>
										Parece que voc j confirmou presena em todos os eventos disponveis. Caso tenha dvidas ou enfrente algum problema, entre em contato com os noivos para obter ajuda.
									</Typography.Paragraph>
								</Render>
							</hgroup>
						</Render>
						<Render condition={Boolean(event)}>
							<hgroup className={clsx([
								'flex flex-col items-center', 'mb-6', 'text-white',
							])}>
								<Typography.Title text={event === EVENTS.EVENT ? 'Cha Bar' : 'Casamento'}/>
								<Typography.Paragraph>
									Agora, selecione as pessoas que iro participar deste evento. Basta clicar nas
									opes abaixo para confirmar a presena dos participantes.
								</Typography.Paragraph>
							</hgroup>
						</Render>
						<ul className={clsx(['rounded-xl'])}>
							<Render condition={Boolean(!event)}>
								{events.map(function (event) {
									return (
										<Item.Event key={event.id} event={event} onClick={handleChooseEvent} />
									)
								})}
							</Render>
							<Render condition={Boolean(event)}>
								{guests.map(function (guest) {
									return (
										<Item.Guest key={guest.id} guest={guest} onChange={handleMarkGuestChecked}/>
									);
								})}
								<button
									type='button'
									onClick={handleConfirmationEvent}
									className={clsx([
										'flex items-center justify-center', 'w-full',
										'p-3 mt-6 font-semibold text-purple-300 text-lg rounded-xl border-[1px] border-purple-300',
									])}
								>
									{'Confirmar'}
								</button>
								<button
									data-id={null}
									type='button'
									onClick={handleChooseEvent}
									className={clsx([
										'flex items-center justify-center', 'w-full',
										'p-3 mt-6 font-semibold text-white text-lg rounded-xl',
									])}
								>
									{'Voltar'}
								</button>
							</Render>
						</ul>
					</div>
				</Render>
				<Render condition={!guests.length}>
					<Render condition={health.status !== HTTP_STATUS.NO_CONTENT}>
						<Typography.Group>
							<Typography.Title text='Aviso!'/>
							<Typography.Paragraph>
								O sistema de confirmao de presena est temporariamente indisponvel para ajustes.
								Estamos trabalhando para resolver isso o mais rpido possvel!
								Tente novamente em breve e, se precisar, entre em contato conosco. Obrigado pela
								compreenso!
							</Typography.Paragraph>
						</Typography.Group>
					</Render>
					<Render condition={health.status === HTTP_STATUS.NO_CONTENT}>
						<Typography.Title className='text-white' text='Confirme aqui!'/>
						<form
							onSubmit={handleSearchGuests}
							className={clsx([
								'flex flex-col', 'w-full md:w-full lg:w-2/3 xl:w-1/2'
							])}
						>
							<legend className='text-white text-justify text-lg mb-4'>
								Digite abaixo cdigo localizado no seu convite para confirmar sua presena.
							</legend>
							<input
								ref={inputRef}
								required
								id='code'
								name='code'
								type='text'
								placeholder='cdigo'
								maxLength={5}
								minLength={5}
								className={clsx([
									'font-semibold font-sans text-purple-400 text-3xl text-center',
									'uppercase rounded-xl mb-6 px-4 py-2',
									'focus:outline-none focus:ring focus:ring-purple-400',
									'placeholder:capitalize placeholder:text-3xl',
								])}
							/>
							<button
								ref={buttonRef}
								type='submit'
								className={clsx([
									'flex items-center justify-center', 'w-full',
									'p-3 font-semibold text-purple-300 text-lg rounded-xl border-[1px] border-purple-300',
								])}
							>
								{!isLoading ? 'Pesquisar' : 'Pesquisando'}
							</button>
						</form>
					</Render>
				</Render>
			</Section.Divider>
		</Section.Group>
	)
}
