import PropTypes from "prop-types";
import { useNotifications } from "reapop";
import { createContext, useCallback, useEffect, useState } from "react";
/**
 * @desc Global
 */
import { HTTP_STATUS, TEXTS } from "~@Constants";
import * as Http from "~@Services/http";

export const HealthCheckContext = createContext(null);

export function HealthCheckProvider({ children }) {
	const notifications = useNotifications();
	const [health, setHealth] = useState({ status: HTTP_STATUS.NO_CONTENT, data: {} });

	const healthCheck = useCallback(async function () {
		try {
			const response = await Http.execute('/health', 'GET', null);
			setHealth(response);
		} catch (error) {
			notifications.notify({
				status: 'error', title: TEXTS.ERROR, message: TEXTS.ERROR_DEFAULT
			});
			setHealth(error);
		}
	}, [notifications]);

	useEffect(() => {
		(async function () {
			await healthCheck();
		})()
	}, [healthCheck]);

	return (
		<HealthCheckContext.Provider value={health}>
			{children}
		</HealthCheckContext.Provider>
	)
}

HealthCheckProvider.propTypes = {
	children: PropTypes.node.isRequired,
}
