import NuBankImage from "~@Assets/nubank.png";
import MercadoPagoImage from "~@Assets/mercado.png";

import { useNotifications } from "reapop";
import { useParams } from "react-router-dom";
import { useCallback, useEffect, useState, useTransition } from "react";
/**
 * @desc Global
 */
import { TEXTS } from "~@Constants";
import { clsx } from "~@Services/clsx";
import { Render } from "~@Components/Render";
import * as Http from "~@Services/http";
import * as scroll from "~@Services/scroll";
import * as Button from "~@Components/Button";
import * as Section from '~@Components/Section';
import * as Typography from '~@Components/Typography';
/**
 * @desc Internal
 */
import { GiftCard } from './Components/GiftCard';

const INITIAL_STATE = {
	id: 0,
	name: '',
	price: 0,
	reserved: false,
	family: null,
	links: {
		pix: '',
		credit: '',
		website: '',
	},
}

export function Checkout() {
	const { id } = useParams();
	const notifications = useNotifications();
	const [gift, setGift] = useState(INITIAL_STATE);
	const [isPending, startTransition] = useTransition();

	const getGift = useCallback(function() {
		startTransition(async function () {
			try {
				const response = await Http.execute(`/api/v1/gift/${id}`, 'GET', null);
				setGift(response.data);
			} catch {
				setGift(INITIAL_STATE);
			}
		});
	}, [id]);

	function handleClickReserve() {
		notifications.notify({
			status: 'warning', title: TEXTS.WARNING, message: 'Para reservar entre em contato com os noivos.'
		});
	}

	useEffect(() => {
		scroll.to('checkout');
		getGift();
	}, [getGift]);

	return (
		<Section.Group>
			<Section.UniqueTitle text='Checkout'/>
			<Section.Divider id='checkout' className={clsx([
				'flex-col items-center', 'p-8 py-20 h-svh w-full', 'bg-dark-800'
			])}>
				{isPending ? (
					<></>
				) : (
					<Render condition={Boolean(gift.id)}>
						<Typography.Group>
							<Typography.Title text='Checkout'/>
							<Render condition={gift.reserved}>
								<Typography.Paragraph className='mb-4'>
									Ol! Parece que voc reservou este item. Para realizar o pagamento ou, caso prefira outro modelo, entre em contato com o noivo ou a noiva.
								</Typography.Paragraph>
							</Render>
							<Render condition={!gift.reserved}>
								<Typography.Paragraph className='mb-4'>
									Confirme o produto e o valor. Aps realizar o pagamento, envie o comprovante para o
									noivo ou a noiva.
								</Typography.Paragraph>
							</Render>
							<GiftCard gift={gift} />
						</Typography.Group>
						<Render condition={Boolean(gift.links.pix)}>
							<Button.Link
								href={gift.links.pix}
								color='text-[#861ad3] border-[#861ad3]'
								text='PIX'
								Icon={<img src={NuBankImage || ''} alt='' width={25}/>}
							/>
						</Render>
						<Render condition={Boolean(gift.links.credit)}>
							<Button.Link
								href={gift.links.credit}
								color='text-[#03b6ec] border-[#03b6ec]'
								text='Parcelado'
								Icon={<img src={MercadoPagoImage || ''} alt='' width={25}/>}
							/>
						</Render>
						<button
							onClick={handleClickReserve}
							className={clsx([
								'flex items-center justify-center', 'p-3 mb-4',
								'font-semibold text-lg rounded-xl border-[1px]',
								'text-gray-400 border-gray-400',
								'w-full md:w-full lg:w-2/3 xl:w-1/2'
							])}
						>
							<p className='ml-2'>{gift.reserved ? 'Reservado' : 'Reservar' }</p>
						</button>
					</Render>
				)}
			</Section.Divider>
		</Section.Group>
	)
}
