import { memo } from "react";
import PropTypes from "prop-types";

import { clsx } from "~@Services/clsx.js";

function DefaultComponent({ id, value, onChange, options }) {
	return (
		<select
			id={id}
			value={value}
			onChange={onChange}
			className={clsx([
				'appearance-none rounded-xl',
				'py-2 px-2 mb-6', 'w-full md:w-full lg:w-2/3 xl:w-1/2'
			])}
		>
			{options.map(function ({ value: v, label: l }, index) {
				return (
					<option key={index} value={String(v)}>{l}</option>
				)
			})}
		</select>
	);
}

DefaultComponent.propTypes = {
	id: PropTypes.string.isRequired,
	onChange: PropTypes.func.isRequired,
	value: PropTypes.string.isRequired,
	options: PropTypes.array.isRequired,
}

export const Default = memo(DefaultComponent);
